import React, { Suspense } from 'react';
import type { Metadata } from 'next';
import { Inter } from 'next/font/google';
import './globals.css';
import { GlobalProvider } from '@/components/GlobalProvider';
import Navbar from '@/components/Navbar';
import CompareTray from '@/components/CompareTray';
import CompareTrayWrapper from '@/components/CompareTrayWrapper';
import { NuqsAdapter } from 'nuqs/adapters/next/app'

const inter = Inter({ subsets: ['latin'] });

export const metadata: Metadata = {
    title: 'AmmoTerminal',
    description: 'Real-time pricing for ammo and firearms',
};

import { getRetailers } from '@/lib/actions';

export default function RootLayout({
    children,
}: {
    children: React.ReactNode;
}) {
    return (
        <html lang="en">
            <body className={`${inter.className} min-h-screen bg-slate-50 font-sans text-slate-900 pb-24 transition-colors duration-300`}>
                <NuqsAdapter>
                    <Suspense fallback={<div className="min-h-screen bg-slate-50 flex items-center justify-center">Loading...</div>}>
                        <RootContent>{children}</RootContent>
                    </Suspense>
                </NuqsAdapter>
            </body>
        </html>
    );
}

async function RootContent({ children }: { children: React.ReactNode }) {
    let retailers: any[] = [];
    try {
        retailers = await getRetailers();
    } catch (error) {
        console.warn('Failed to fetch retailers in RootLayout:', error);
    }

    return (
        <GlobalProvider initialRetailers={retailers}>
            <Navbar />
            <main>
                {children}
            </main>
            <CompareTrayWrapper />
        </GlobalProvider>
    );
}
